import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import { PlusOutlined, SearchOutlined } from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import { Button } from "antd";
import EditModal from "../../util/EditModal";
import carEditForm from "./carEditForm";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import { Helmet } from "react-helmet";

class Car extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      editModalVisible: false,
      editModalId: null,
    };
  }
  m = (key, prefix = "car.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFininsh = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFininsh) {
      this.props.onMethodFininsh();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return { editModalVisible: !state.editModalVisible, editModalId };
    });
  };

  editModalFininsh = () => {
    this.editModalToggle();
    this.onMethodFininsh();
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: (
            <>
              <Button
                className="gx-mr-4"
                onClick={(e) => {
                  e.stopPropagation();
                  this.editModalToggle();
                }}
                icon={<PlusOutlined />}
              />
              {this.m("label")}
            </>
          ),
          dataIndex: "label",
          sorter: true,
          width: "65%",
          render: (value, item) => (
            <>
              <div
                className="gx-pointer gx-text-primary"
                onClick={() => this.editModalToggle(item.id)}
              >
                <div>{value}</div>

                {item.identity && (
                  <div className="gx-font-weight-light gx-text-small ">
                    {item.identity}
                  </div>
                )}
                {item.description && (
                  <div className="gx-font-weight-light gx-text-small gx-text-muted">
                    {item.description}
                  </div>
                )}
              </div>
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          dataIndex: "chargernameac",
          title: this.m("chargernameac"),
          sorter: true,
          render: (value, item) => (
            <>
              <div>{value}</div>
            </>
          ),
        },

        {
          width: "5%",
          title: this.m("created"),
          dataIndex: "created",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },

        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFininsh} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>{this.m("car")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("car")}</h2>
        <EditModal
          essence="car"
          onClose={this.editModalToggle}
          form={carEditForm.call(this)}
          onFinish={this.editModalFininsh}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
        />
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/car/list"}
        />
      </div>
    );
  }
}

export default injectIntl(Car);
